.form {
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 0 auto 50px auto;

  &--error {

    margin: 0 0 20px 0;
    font-size: 15px;
    color: #f33004;
  }

  @media screen and (min-width: 768px) {
    max-width: 800px;

    &--error {
      margin-left: 30%;
    }

  }
}

.form__title {
  font-size: 25px;
  margin: 15px 0;
  text-align: center;
  color: #923205;

  @media screen and (min-width: 768px) {
    font-size: 30px;
  }
}

.form__subtitle {
  font-size: 22px;
  margin: 0 0 30px 0;
  text-align: center;
  color: #923205;

  @media screen and (min-width: 768px) {
    font-size: 25px;
  }
}

.form__fieldset {
  margin-bottom: 30px;
  padding: 5px 40px;
  background-color: #fef7ef;
  border: 2px solid #05b317;
  border-radius: 7px;
}

.form__legend {
  position: relative;
  top: -5px;
  padding: 0 10px;
  color: #05b317;
  font-weight: 500;
  font-size: 18px;
  text-align: center;

  @media screen and (min-width: 400px) {
    text-align: left;
  }
  @media screen and (min-width: 768px) {
    font-size: 22px;
  }
}

.form__item {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 7px;
  margin: 0 0 10px 0;
  color: #923205;

  &--position--left {
    justify-content: flex-start;
  }

  &--position--relative {
    position: relative;
  }

  @media screen and (min-width: 768px) {
    justify-content: space-between;
    flex-wrap: nowrap;
    gap: 10px;
    margin: 0 0 10px 0;

    &--position--left {
      justify-content: flex-start;
    }
  }
}

.form__input {
  width: 100%;
  padding: 7px;
  font-size: 14px;
  font-family: inherit;
  border: 1px solid #923205;


  &--error {
    border-color: #f33004;
    outline: none;
  }

  @media screen and (min-width: 768px){
    width: 70%;
    font-size: 16px;
  }
}

.form__mess {
  width: 100%;
  height: 100px;
  padding: 7px;
  font-size: 14px;
  font-family: inherit;
  border: 1px solid #923205;

  @media screen and (min-width: 768px){
    width: 70%;
    font-size: 16px;
  }
}

.form__input::placeholder,
.form__mess::placeholder {
  font-family: inherit;
  color: #cccccc;
  font-size: 14px;

  @media screen and (min-width: 768px){
    font-size: 16px;
  }
}

.form__select {
  position: relative;
  z-index: 0;
  width: 100%;
  padding: 7px;
  font-size: 14px;
  border: 1px solid #923205;

  &--error {
    border-color: #f33004;
    outline: none;
  }

  @media screen and (min-width: 768px){
    width: 72%;
    font-size: 16px;
  }
}

.form__option-first {
  position: absolute;
  top: 36px;
  left: 10px;
  right: 21px;
  font-family: inherit;
  color: #cccccc;
  font-size: 14px;
  z-index: 5;
  background-color: #ffffff;

  @media screen and (min-width: 768px){
    top: 5px;
    left: 31%;
    font-size: 16px;
  }
}

.form__label {
  width: 100%;
  cursor: pointer;

  &--wide {
    width: 75%;
  }
  @media screen and (min-width: 768px){
    width: 30%;

    &--wide {
      width: 85%;
    }
  }
}

.form__check {
  width: 20px;
  margin: 0 15px 0 0;

  @media screen and (min-width: 768px){

  }
}

.form__question {
  color: #923205;
  font-weight: 500;
}

.form__btn {
  width: max-content;
  padding: 10px;
  color: #fef7ef;
  background-color: #05b317;
  border: 1px solid #923205;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #059918;
  }
}

.form__success {
  margin:0 0 30px 0;
  color:#05b317;
}
